@media only screen and (max-width: 1200px) {}

@media only screen and (max-width: 767px) {}






/* Custom, iPhone Retina */

@media only screen and (min-width: 320px) and (max-width: 767px) {
    .feature-panel {
        max-width   : 370px;
        margin-right: auto;
        margin-left : auto;
    }
}

